import "./Topbar.scss"
import { Link } from "react-router-dom"

export default function Topbar ()
{
    return (
        <div className="topbar">
            <ul>
                <li><Link to='/'>Introduction</Link></li>
                <li><Link to='/portfolio'>Portfolio</Link></li>
                <li><Link to='/works'>Works</Link></li>

            </ul>
        </div>
    )
}